import { _decorator, Component, JsonAsset, Node, resources, SpriteFrame } from 'cc';
const { ccclass, property } = _decorator;
import { CardData } from './CardData';

@ccclass('DeckManager')
export class DeckManager extends Component {
    start() {

    }

    deck: CardData[] = [];

    onLoad() {
        resources.load('deck.json', JsonAsset, (err, asset) => {
            if (err) {
                console.error('Failed to load card data:', err);
                return;
            }
            this.deck = asset.json as CardData[];
            this.loadSprites();
        });
    }

    loadSprites() {
        for (const card of this.deck) {
            resources.load(`sprites/${card.sprite}.png`, SpriteFrame, (err, spriteFrame) => {
                if (!err) card.spriteFrame = spriteFrame;
            });
        }
    }

    update(deltaTime: number) {
        
    }
}

